.hotel
    padding-top: 23px
    padding-bottom: 70px
    &-info
        display: flex
        align-items: center
        justify-content: space-between
        padding-bottom: 20px
        &__offer
            margin-left: 14px
            margin-top: 4px   
        &__name
            margin-top: 4px   
    &-name
        font-size: 24px
        line-height: 30px
        font-weight: bold
    &-wrapper 
        display: flex
        flex-wrap: wrap
        align-items: center
    &-description
        font-size: 12px
        line-height: 15px
    &-grid
        display: flex
        justify-content: space-between
    &-right
        display: flex
        flex-direction: column
        justify-content: space-between
    &-slider
        width: 728px
        height: 450px
        margin-left: 0
        &__item
            width: 100%
            height: 100%
            position: relative
            border-radius: 8px
            overflow: hidden
            &::after
                content: ''
                position: absolute
                width: 100%
                height: 100%
                top: 0
                bottom: 0
                left: 0
                right: 0
                background: linear-gradient(180deg, rgba(0, 122, 223, 0.48) 0%, rgba(255, 255, 255, 0.128) 100%)
                z-index: 1
        &__button 
            position: absolute
            z-index: 2
            top: 50%
            transform: translateY(-50%)
            width: 30px
            height: 30px
            border: none
            cursor: pointer
            background-repeat: no-repeat
            background-position: center
            background-size: 10px
            background-color: transparent
            &_next
                right: 18px
                background-image: url(../icons/arrow-next.svg)
            &_prev
                left: 18px
                background-image: url(../icons/arrow-prev.svg)
.stars
    flex-basis: 100%
.offer
    font-size: 10px
    line-height: 13px
    padding: 4px 9px
    background-color: $primary-color
    border-radius: 3px
    color: $light-color

.rating
    max-width: 65px
    min-height: 45px
    background-color: $primary-color
    color: $light-color
    text-align: center
    border-radius: 4px
    padding-top: 3px
    &__text
        font-size: 8px
        line-height: 10px
    &__counter
        font-size: 14px
        line-height: 18px
  



.booking
    background: $dark-color
    border-radius: 8px 
    width: 350px
    min-height: 210px
    color: $light-color
    padding: 28px
    font-family: 'Nunito'
    font-weight: 400
    &__info
        display: flex
        justify-content: space-between
        margin-bottom: 9px
    &__price
        display: flex
        flex-direction: column
    &__start
        font-size: 10px
        line-height: 14px
    &__pricetag
        font-size: 24px
        line-height: 33px
        font-weight: 800
    &__per-room
        font-size: 12px
        line-height: 16px
    &__room
        margin-top: 4px
    &__text
        font-size: 12px
        line-height: 16px
        display: flex
        align-items: center
        justify-content: space-between
        margin-bottom: 8px
    &__description
        margin-left: 9px
    &__call-center
        display: flex
        justify-content: space-between
        align-items: center
        margin-bottom: 17px
    &__heading
        font-weight: 600
        font-size: 18px
        line-height: 25px
    &__number
        display: flex
        align-items: center
        justify-content: space-between
        font-weight: 600
        font-size: 18px
        line-height: 25px
        color: inherit
        text-decoration: none
    &__num
        margin-left: 8px
    &__button
        font-weight: 600
        font-size: 14px
        line-height: 19px
        background-color: $primary-color
        border-radius: 4px
        width: 100%
        min-height: 45px
        border: none
        cursor: pointer
        color: inherit
.map
    width: 347px
    height: 213px
    border-radius: 10px
    overflow: hidden